<template>
    <div class="enemy-wrapper">
        <h3 class="enemy-name">
            {{enemyList[monster]}}
        </h3>
        <div class="enemy-health">
            {{health}}
        </div>
    </div>
    <button @click="inflictDamage" class="main-button">
        Ударить
    </button>
</template>

<script>
    import {ref} from "vue";

    export default {
        name: "Enemy",
        setup() {
            const rnd = (min,max) => {
                return Math.floor(Math.random() * (max - min + 1)) + min;
            }
            let enemyList = ref(['Мега-кроль', 'Бешенная белка', 'Злобный карасик', "Большой енот", "Лось"])
            let enemyListLength = enemyList.value.length-1
            let health = ref(10)
            let staticHealth = ref(10)
            let monster = rnd(0,enemyListLength)
            let killedMonster = 0

            return {
                enemyList,
                enemyListLength,
                health,
                monster,
                staticHealth,
                rnd,
                killedMonster
            }
        },
        methods: {
            updateMonster() {
                this.killedMonster++
                this.$store.commit('killingEnemy')
                if(this.killedMonster === 20) {
                    this.enemyList = ['Царь мелких животных - Кот Барсик']
                    this.staticHealth = 100
                    this.health = 100
                }
                if(this.killedMonster === 21) {
                    this.$store.commit('addMoney', 10)
                }
                if(this.killedMonster > 20) {
                    this.enemyList = ['Большая черепаха', 'Саша-Камень', "Сбежавший пельмень", "Злая лиса", "Волк-Дрищ"]
                }
                if(this.killedMonster === 40) {
                    this.enemyList = ['Волк-Не-Дрищ']
                    this.staticHealth = 1000
                    this.health = 1000
                }
                if(this.killedMonster === 41) {
                    this.$store.commit('addMoney', 25)
                }
                this.monster = this.rnd(0,this.enemyList.length-1)
            },
            inflictDamage() {
                this.health = this.health - this.$store.state.clickPower

                if(this.health <= 0) {
                    this.staticHealth = this.staticHealth * 1.1
                    let updatedHealth = Math.round(this.staticHealth)
                    this.health = updatedHealth
                    this.updateMonster()
                }
            },
        }
    }
</script>

<style scoped>
    .enemy-wrapper{
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .enemy-health{
        margin: 0 0 0 10px;
    }
    .main-button{
        padding: 15px 25px;
        background: rgba(135,206,250,0.7);
        cursor: pointer;
        border: none;
        border-radius: 5px;
        color: #fff;
        margin: 0 0 15px 0;
    }
    .main-button::after{
        content: url("../assets/punch-icon.png");
        position: relative;
        top: 3px;
    }
    .main-button:active{
        background: rgba(135,206,250,1);
    }
</style>