import { createApp } from 'vue'
import { createStore } from 'vuex'
import App from './App.vue'

const store = createStore({
    state () {
        return {
            clickPower: 1,
            moneyIndex: 0,
        }
    },
    mutations: {
        incrementClickPower (state, indexUp) {
            state.clickPower += indexUp
        },
        killingEnemy(state) {
            state.moneyIndex += 1
        },
        setMoneyIndex(state, index) {
            state.moneyIndex = index
        },
        addMoney(state, index) {
            state.moneyIndex += index
        }
    }
})
const app = createApp(App)
app.use(store)
app.mount('#app')

