<template>
    <div class="shop-open">
        <button v-on:click="setActive" class="btn-openShop">
            Открыть магазин
        </button>
    </div>
    <div class="shop-wrapper" v-if="isActive">
        <ul class="upgrade-list">
            <li class="upgrade-item">
                <div class="item-title">
                    Подкачать бицепс
                </div>
                <button @click="UpgradeClick(3,1)" class="buy-item">
                    Купить за 3
                </button>
            </li>
            <li class="upgrade-item">
                <div class="item-title">
                    Купить крутую палку
                </div>
                <button @click="UpgradeClick(5,3)" class="buy-item">
                    Купить за 5
                </button>
            </li>
            <li class="upgrade-item">
                <div class="item-title">
                    Купить Корсар 1
                </div>
                <button @click="UpgradeClick(7,5)" class="buy-item">
                    Купить за 7
                </button>
            </li>
            <button @click="setActive" class="exit-shop">
                Выйти из магазина
            </button>
        </ul>

    </div>
</template>

<script>
    import {ref} from "@vue/reactivity";

    export default {
        name: "Shop",
        setup() {
            let isActive = ref(false)
            return {
                isActive,
            }
        },
        methods: {
            upClickPower(indexPower) {
                this.$store.commit('incrementClickPower', indexPower)
            },
            setActive() {
                if(!this.isActive){
                    this.isActive = true
                } else{
                    this.isActive = false
                }
            },
            UpgradeClick(itemPrice, clickPowerIndex) {
                if(itemPrice <= this.moneyCount) {
                    this.buyItem(this.moneyCount-itemPrice)
                    this.upClickPower(clickPowerIndex)
                } else {
                    alert("У вас недостаточно монет")
                }
            },
            buyItem(index) {
                this.$store.commit('setMoneyIndex', index)
            }
        },
        computed: {
            moneyCount() {
                return this.$store.state.moneyIndex
            }
        }
    }

</script>

<style scoped>
    li{
        list-style-type: none;
    }
    .shop-wrapper{
        position: fixed;
        display: flex;
        z-index: 10;
        width: 100%;
        height: 20%;
        left: 36vw;
        right: 50%;
        top: 12%;
        bottom: 50%;
    }
    .upgrade-list{
        padding: 0;
        margin: 0;
        display: flex;
        align-content: center;
        flex-direction: column;
        justify-content: center;
        padding: 100px 200px;
        background: rgba(255,255,255,1);
        box-shadow: 0 0 1px 1000px rgba(0,0,0,0.9);
    }
    .exit-shop{
        margin-top: 20px;
    }
</style>